import { ReactNode } from 'react';

interface ButtonProps {
  children: ReactNode;
  variant?: 'primary' | 'secondary';
  icon?: string;
  className?: string;
  onClick?: () => void;
  type?: 'button' | 'submit' | 'reset';
}

export default function Button({ 
  children, 
  variant = 'primary', 
  icon, 
  className = '',
  ...props 
}: ButtonProps) {
  const baseClasses = 'inline-flex items-center gap-2 px-5 py-2 rounded-lg font-semibold text-sm cursor-pointer transition-[var(--transition)]';
  const variantClasses = {
    primary: 'bg-[var(--primary)] text-white shadow-md hover:bg-[var(--primary-hover)] hover:-translate-y-0.5 hover:shadow-lg',
    secondary: 'bg-[var(--secondary)] text-white shadow-md hover:bg-[#05c295] hover:-translate-y-0.5 hover:shadow-lg'
  };

  return (
    <button 
      className={`${baseClasses} ${variantClasses[variant]} ${className}`} 
      {...props}
    >
      {icon && <i className={`fas fa-${icon}`}></i>}
      {children}
    </button>
  );
}