import { ReactNode } from 'react';

interface CardProps {
  children: ReactNode;
  className?: string;
  hoverEffect?: boolean;
}

export default function Card({ 
  children, 
  className = '', 
  hoverEffect = true 
}: CardProps) {
  return (
    <div 
      className={`bg-white rounded-xl border border-[var(--border)] p-6 shadow-[var(--card-shadow)] transition-[var(--transition)] ${
        hoverEffect ? 'hover:shadow-lg' : ''
      } ${className}`}
    >
      {children}
    </div>
  );
}