import Image from 'next/image';
import { Review } from '../../types/business';

interface ReviewItemProps {
  review: Review;
}

export default function ReviewItem({ review }: ReviewItemProps) {
  return (
    <div className="flex gap-4 py-5 border-b border-[var(--border)] transition-[var(--transition)] hover:bg-[rgba(248,249,250,0.5)]">
      <Image 
        src={review.avatar} 
        alt={review.author} 
        width={48} 
        height={48} 
        className="rounded-full border-2 border-white shadow-sm"
      />
      <div className="flex-1">
        <div className="flex justify-between items-center mb-2">
          <div className="review-author font-bold text-sm">{review.author}</div>
          <div className="review-rating text-[var(--action)] text-sm">
            {Array.from({ length: 5 }).map((_, i) => (
              <i 
                key={i}
                className={`fas ${i < Math.floor(review.rating) ? 'fa-star' : 
                  (i === Math.floor(review.rating) && review.rating % 1 > 0 ? 'fa-star-half-alt' : 'fa-star')}`}
              />
            ))}
          </div>
        </div>
        <div className="review-text text-sm text-[var(--text-dark)] leading-relaxed">
          {review.text}
        </div>
      </div>
    </div>
  );
}