import Image from 'next/image';
import { Product } from '../../types/business';

interface ProductCardProps {
  product: Product;
}

export default function ProductCard({ product }: ProductCardProps) {
  return (
    <div className="border border-[var(--border)] rounded-lg overflow-hidden transition-[var(--transition)] bg-white hover:-translate-y-1 hover:shadow-lg">
      <Image 
        src={product.image} 
        alt={product.name} 
        width={300} 
        height={200} 
        className="w-full h-36 object-cover border-b border-[var(--border)]"
      />
      <div className="p-4">
        <div className="product-name font-semibold text-sm mb-2">{product.name}</div>
        <div className="product-price text-[var(--primary)] font-bold text-lg">{product.price}</div>
      </div>
    </div>
  );
}