import MainHeader from '../../components/layouts/MainHeader';
import BusinessSidebar from '../../components/business/BusinessSidebar';
import BusinessStats from '../../components/business/BusinessStats';
import ProductsGrid from '../../components/business/ProductsGrid';
import ReviewItem from '../../components/business/ReviewItem';
import Button from '../../components/ui/Buttom';
import Card from '../../components/ui/Card';
import { Review } from '../../types/business';

const reviews: Review[] = [
    {
      id: 1,
      author: 'María González',
      avatar: 'https://randomuser.me/api/portraits/women/44.jpg',
      rating: 5,
      text: '¡Los productos son excelentes! El kit zero waste ha cambiado completamente cómo manejo los residuos en casa. Muy buena calidad y el envío fue súper rápido.'
    },
    {
      id: 2,
      author: 'Carlos Mendoza',
      avatar: 'https://randomuser.me/api/portraits/men/22.jpg',
      rating: 4.5,
      text: 'Buenísima la botella térmica, mantiene el calor por horas. Solo sugeriría más colores disponibles. El servicio al cliente fue muy atento.'
    }
  ];
  
  export default function BusinessDashboard() {
    return (
      <div className="min-h-screen">
        <MainHeader />
        
        <div className="business-container max-w-7xl mx-auto px-6 py-6 flex flex-col md:flex-row gap-6">
          <BusinessSidebar />
          
          <main className="flex-1">
            {/* Tarjeta de bienvenida */}
            <Card className="fade-in mb-6">
              <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-5">
                <h2 className="text-xl font-bold flex items-center gap-3">
                  <i className="fas fa-store text-[var(--primary)]"></i>
                  Resumen de tu negocio
                </h2>
                <Button variant="primary" icon="pencil-alt">
                  Editar negocio
                </Button>
              </div>
              
              <p className="mb-5">Aquí puedes gestionar todos los aspectos de tu negocio. Revisa tus estadísticas, productos y conexiones con clientes.</p>
              
              <BusinessStats />
            </Card>
            
            {/* Productos destacados */}
            <Card className="fade-in fade-in-delay-1 mb-6">
              <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-5">
                <h2 className="text-xl font-bold flex items-center gap-3">
                  <i className="fas fa-box-open text-[var(--secondary)]"></i>
                  Productos destacados
                </h2>
                <Button variant="primary" icon="plus">
                  Agregar producto
                </Button>
              </div>
              
              <ProductsGrid />
            </Card>
            
            {/* Reseñas recientes */}
            <Card className="fade-in fade-in-delay-2">
              <div className="mb-5">
                <h2 className="text-xl font-bold flex items-center gap-3">
                  <i className="fas fa-star text-[var(--action)]"></i>
                  Reseñas recientes
                </h2>
              </div>
              
              <div>
                {reviews.map(review => (
                  <ReviewItem key={review.id} review={review} />
                ))}
              </div>
            </Card>
          </main>
        </div>
  
        {/* Estilos CSS */}
        <style jsx>{`
          /* Estilos base mejorados */
          * {
            margin: 0;
            padding: 0;
            box-sizing: border-box;
            font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, sans-serif;
          }
          
          :root {
            --primary: #4361ee;
            --primary-hover: #3a56d4;
            --secondary: #06d6a0;
            --accent: #7209b7;
            --action: #f72585;
            --bg-light: #f8f9fa;
            --text-dark: #212529;
            --text-gray: #6c757d;
            --border: #dee2e6;
            --card-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
            --transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
          }
          
          .min-h-screen {
            background-color: var(--bg-light);
            color: var(--text-dark);
            line-height: 1.6;
          }
          
          /* Header estilizado (Nota: MainHeader ya lo incluye, estos estilos son referencia) */
          header {
            background-color: white;
            border-bottom: 1px solid var(--border);
            position: sticky;
            top: 0;
            z-index: 1000;
            padding: 0;
            box-shadow: var(--card-shadow);
          }
          
          /* Contenedor principal */
          .business-container {
            display: flex;
            max-width: 1200px;
            margin: 1.5rem auto;
            gap: 1.5rem;
            padding: 0 1.5rem;
          }
          
          /* Sidebar de negocio (estilos base, BusinessSidebar los sobreescribe) */
          .business-sidebar {
            width: 280px;
            flex-shrink: 0;
          }
          
          /* Contenido principal */
          .flex-1 {
            flex: 1;
          }
          
          .business-card {
            background-color: white;
            border-radius: 12px;
            border: 1px solid var(--border);
            padding: 1.5rem;
            margin-bottom: 1.5rem;
            box-shadow: var(--card-shadow);
            transition: var(--transition);
          }
          
          .business-card:hover {
            box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
          }
          
          .card-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 1.25rem;
          }
          
          .card-title {
            font-size: 1.25rem;
            font-weight: 700;
            display: flex;
            align-items: center;
            gap: 0.75rem;
          }
          
          .card-title i {
            font-size: 1.1rem;
          }
          
          /* Efectos especiales */
          @keyframes fadeIn {
            from { opacity: 0; transform: translateY(10px); }
            to { opacity: 1; transform: translateY(0); }        
          }
          
          .fade-in {
            animation: fadeIn 0.5s ease-out forwards;
          }
          
          .fade-in-delay-1 {
            animation-delay: 0.1s;
          }
          
          .fade-in-delay-2 {
            animation-delay: 0.2s;
          }
        `}</style>
      </div>
    );
  }